:root {
  /* Primary Color */
  --primary-color: hsl(213, 22%, 16%); /* #202832 */
  --primary-color-80: hsl(213, 22%, 16%, 0.8);
  --primary-color-60: hsl(213, 22%, 16%, 0.6);
  --primary-color-40: hsl(213, 22%, 16%, 0.4);
  --primary-color-20: hsl(213, 22%, 16%, 0.2);

  /* Secondary Color */
  --secondary-color: hsl(215, 22%, 51%); /* #677E9E */
  --secondary-color-80: hsl(215, 22%, 51%, 0.8);
  --secondary-color-60: hsl(215, 22%, 51%, 0.6);
  --secondary-color-40: hsl(215, 22%, 51%, 0.4);
  --secondary-color-20: hsl(215, 22%, 51%, 0.2);

  /* Accented Colors */
  --accented-color: hsl(31, 74%, 71%); /* #ECB77E */
  --accented-color-80: hsl(31, 74%, 71%, 0.8);
  --accented-color-60: hsl(31, 74%, 71%, 0.6);
  --accented-color-40: hsl(31, 74%, 71%, 0.4);
  --accented-color-20: hsl(31, 74%, 71%, 0.2);

  /* White Colors */
  --white-color: hsl(0, 0%, 100%, 100%); /* FFFFFF */
  --white-color-15: hsl(0, 0%, 100%, 15%);
  --white-color-40: hsl(0, 0%, 100%, 40%);

  /* Grey Color Scala */
  --grey-color: hsl(0, 0%, 77%); /* #C4C4C4 */
  --grey-color-80: hsl(0, 0%, 77%, 0.8);
  --grey-color-60: hsl(0, 0%, 77%, 0.6);
  --grey-color-40: hsl(0, 0%, 77%, 0.4);
  --grey-color-20: hsl(0, 0%, 77%, 0.2);

  /* Grey Colors */
  --light-grey: hsl(0, 0%, 98%);
  --medium-grey: hsl(0, 0%, 77%);
  --dark-medium-grey: hsl(0, 0%, 56%);

  /* Labels */
  --nation-color: hsl(4, 59%, 69%); /* #DE574E */
  --world-color: hsl(48, 84%, 64%); /* #F0D158 */
  --politics-color: hsl(218, 83%, 56%); /* #3478EC */
  --business-color: hsl(257, 92%, 59%); /* #6E38F6 */
  --regional-color: hsl(172, 46%, 58%); /* #61C5B8 */
  --culture-color: hsl(18, 87%, 65%); /* #F38758 */
  --food-color: hsl(14, 61%, 75%); /* #E6AB99 */
  --family-color: hsl(227, 51%, 71%); /* #8E9EDA */
  --health-color: hsl(250, 84%, 75%); /* #9B88F5 */
  --weather-color: hsl(333, 74%, 75%); /* #EF92BC */
  --sport-color: hsl(333, 74%, 75%); /* #92D9D6 */
  --science-color: hsl(147, 44%, 64%); /* #7BCCA0 */
  --music-color: hsl(263, 68%, 68%); /* #A177E5 */
  --tech-color: hsl(32, 93%, 66%); /* #F9B05A */

  /* Statusbar */
  --statusbar-backgroundcolor: hsl(32, 66%, 94%); /* FAF1E7 */
  --statusbar-warning: hsl(0, 74%, 71%); /* EC7E7E */
  --statusbar-approved: hsl(101, 63%, 70%); /* A1E382 */
  --statusbar-neutral: hsl(31, 74%, 71%); /* ECB77E */
  --statusbar-onhold: hsl(263, 68%, 68%); /* A177E5 */
}

