  --tech-color: hsl(32, 93%, 66%); /* #F9B05A */

  /* Statusbar */
  --statusbar-backgroundcolor: hsl(32, 66%, 94%); /* FAF1E7 */
  --statusbar-warning: hsl(0, 74%, 71%); /* EC7E7E */
  --statusbar-approved: hsl(101, 63%, 70%); /* A1E382 */
  --statusbar-neutral: hsl(31, 74%, 71%); /* ECB77E */
  --statusbar-onhold: hsl(263, 68%, 68%); /* A177E5 */
}

.login-page {
  display: flex;
}

.image-container {
  display: flex;
  height: 100vh;
  width: 820px;
}

.login-form {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.login-form-container {
  align-items: center;
  width: 420px;
  height: 310px;
}

.input-field {
  margin: 15px 0;
}

.input-box {
  width: 370px;
  height: 50px;
  border: 2px solid var(--grey-color-60);
  border-radius: 7px !important;
}

.sign-in-container {
  margin-bottom: 40px;
}

.submit-button {
  background-color: var(--primary-color);
  color: var(--white-color);
  border-radius: 7px;
  cursor: pointer;
  border: none;
  margin: 25px 0;
  width: 370px;
  height: 50px;
}



.logout-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logout-button {
  border: none;
  cursor: pointer;
  background-color: white;
}

