@import url(./../../../index.css);

.dropdown {
  margin-top: 20px;
  margin-bottom: 20px;
}

.dropdown-select {
  width: 460px;
  height: 37px;
  user-select: none;
}

.dropdown-btn {
  box-shadow: 0px 4px 8px 2px rgba(0, 0, 0, 0.05);
  background-color: var(--white-color);
  border-radius: 7px;
  color: var(--primary-color-60);
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  text-align: center;
  padding: 10px;
  width: 460px;
}

.space {
  width: 50px;
  background-color: transparent;
  flex: 2;
}

.dropdown-content {
  border: 1px solid var(--primary-color);
  border-radius: 6px;
  margin-top: 10px;
  background-color: var(--white-color);
  position: absolute;
  width: 478px;
}

.dropdown-item {
  color: var(--primary-color);
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  transition: all 0.2s;
}

.dropdown-item:hover {
  background-color: var(--accented-color-40);
  cursor: pointer;
}

.selected-visibility {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  color: black;
}
